<template>
  <div>
    <heder></heder>
    <broad></broad>
    <div class="content">
      <el-button type="text" @click="dialogVisible = true"></el-button>
      <el-button type="primary"></el-button>
      <template>
        <el-table
          :data="tableData"
          highlight-current-row

          border
          style="width: 100%">
          <el-table-column
            prop="shopName"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="goodsName"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="brand"
            label="">
          </el-table-column>
          <el-table-column
            label="" width="180"
          >
            <template slot-scope="scope">
            <span @click="showtime = true" >
              <el-button type="primary" size="small"  @click="update(scope.row)"></el-button>
            </span>
              <!--<el-button type="danger" size="small" @click="del(scope.row.id)"></el-button>-->
            </template>
          </el-table-column>

          <el-table-column
            label="" width="180"
          >
            <template slot-scope="scope">

              <el-button type="primary" size="small"  @click="become(scope.row)" v-text="TOguo==false? '':''"></el-button>


            </template>
          </el-table-column>
          <el-table-column
            label="" width="180"
          >
            <template slot-scope="scope">

              <el-button type="primary" size="small"  @click="branch(scope.row)"></el-button>


            </template>
          </el-table-column>

        </el-table>
      </template>

      <!---->
      <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <el-col :span="24" class="dev">
          <span>ID</span>
          <el-select v-model="from.input1" placeholder="" >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>

        <br>

        <br>
        <span></span>
        <el-input v-model="from.input2" placeholder=""></el-input>
        <br>
        <el-col :span="24" class="dev">
          <span></span>
          <el-select v-model="from.input3" placeholder="" >
            <el-option
              v-for="item in options4"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>

        <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
          <span @click="dialogVisible = false">
           <el-button type="primary" @click="breas()"> </el-button>
          </span>

  </span>
      </el-dialog>


      <!---->
      <el-dialog
        title=""
        :visible.sync="showtime"
        width="30%"
        :before-close="handleClose">
        <span>ID</span>
        <el-input v-model="percentage.goodsId" placeholder=""  :disabled="true"></el-input>
        <br>
        <span></span>
        <el-input v-model="percentage.percentageBonus" placeholder=""></el-input>
        <br>
        <span>ID</span>
        <el-input v-model="percentage.agentId" placeholder=""  :disabled="true"></el-input>
        <br>
        <span>ID</span>
        <el-input v-model="percentage.id" placeholder=""  :disabled="true"></el-input>
        <br>


        <span slot="footer" class="dialog-footer">
    <el-button @click="showtime = false"> </el-button>
          <span @click="showtime = false">
           <el-button type="primary" @click="surew()"> </el-button>
          </span>

  </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import heder from '@/components/heder'
  import broad from '@/components/broadside'
  import {Slib,Sadd,Seidt,Saudit,Schoice,Sport,Blie} from '../../axios/ajax/ajax'



  var ck={
    input1:"",
      input2:"",
      input3:"",
      input4:"",}

  export default {
    name: 'xxx',
    data(){
      return {
        dialogVisible: false,
        showtime: false,
        tableData: [

        ],
        percentage:{},
        options:[],
        options4:[],
        TOguo:false,

        from:{
            input1:"",
          input2:"",
          input3:"",
          input4:"",}

      }
    },
    mounted(){
      //-B
      Schoice({}).then((res) => {
        if (res.code==200) {
          let DataList = res.data;
          let dir = [];
          for (let i = 0; i < DataList.length; i++) {
            dir[i] = {
              value: DataList[i].agentId,
              label: DataList[i].agentName
            }
          }
          this.options = dir;
        }
      }).catch((res) => {
        this.$message({
          message: res.message,
          type: 'error'
        });
      });

//id
      Blie({}).then((res) => {
        if (res.code==200) {
          let DataLis = res.data.list;
          let die = [];
          for (let i = 0; i < DataLis.length; i++) {
            die[i] = {
              value: DataLis[i].agentId,
              label: DataLis[i].shopName
            }
          }
          this.options4 = die;
        }
      }).catch((res) => {
        this.$message({
          message: res.message,
          type: 'error'
        });
      });
    },
    created(){


      Slib({}).then((res) => {

        if(res.code==200){
          this.tableData=res.data.list
        }

      });

    },
    methods: {
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      breas(){

        console.log(this.value);
        console.log(this.value1);
        var   list={
            goodsId:this.from.input1,
            percentage:this.from.input2,
            agentId:this.from.input3,

          }

        Sadd(list).then((res) => {

          if(res.code==200){

            this.init(this.form,ck)

          }

        });
      },
      init(a,b){
        for(let i in a){
          a[i] = b[i]
        }
      },
      update(all){
        console.log(all);
        this.percentage=all


  },
      become(ol){
        Saudit({status:0,bonusId:ol.id}).then((res) => {

          if(res.code==200){
            this.TOguo=true
          }

        });

  },
      surew(){

        Seidt({goodsId:this.percentage.goodsId,percentage:this.percentage.percentageBonus,agentId:this.percentage.agentId,
          bonusId:this.percentage.id}).then((res) => {

          if(res.code==200){

          }

        });
      },
      branch(AO){
        Sport({brand:AO.brand}).then((res) => {

          if(res.code==200){

          }

        });

      }


    },
    components: {
      heder,
      broad
    }

  }
</script>

<style lang="scss" scoped>
  .content {
    background: none repeat scroll 0 0 #fff;
    position: absolute;
    left: 210px;
    right: 0;
    top: 70px;
    bottom: 0;
    width: auto;
    padding: 40px;
    box-sizing: border-box;
    overflow-y: scroll;
  }
</style>



