import Vue from 'vue';

import store from "./store";
import {getUser} from '../axios/login/index'
import Vuex from 'vuex';
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    navShow: 0,
    token:'',
    user:null,
    carts:[],
    newslist:[],
    newsname:'',
    id:'',
    indent:[],
    Delid:[],
    totalPrice:''

  },//state 
  actions: {
    getNav({commit}, num){
      return new Promise((resolve, reject) => {
        commit('SET_NAV', num);
        resolve();
      })
    },
    LoginByToken({commit},token){
      //token ---userinfo
      return new Promise((resolve,reject)=>{
        commit('SET_TOKEN', token);
        sessionStorage.setItem('TOKEN',token);

        if(store.getters.getUserInfo ===null){
          getUser({}).then((res)=>{
            if(res.code ==200){
              commit('SET_USER_INFO', res.data);
            }
          })
        }
        resolve();

      })
    }
  },//Actions ,
  // store.commit() Store 
  // dispatch Actions
  mutations: {
    SET_NAV:function(state,num){
      this.state.navShow =  num;
    },
    SET_TOKEN:function (state,token) {
      this.state.token = token;
    },
    SET_USER_INFO:function (state,data) {
      this.state.user = data.userinfo;
    },
    addToCart(state, goods){

      // console.log(goods);
      //cart {id: 18, count: 1}

      //localStorage
      var carts = localStorage.getItem("carts");
      carts = carts ? JSON.parse(carts) : [];

      //id


      var isExsit = false;

      carts.forEach(v=>{
        //count
        if(v.id == goods.id){
          v.count += goods.count;
          isExsit = true;
        }
      })

      //push
      if(!isExsit){
        carts.push(goods);
      }

      //localstorage
      localStorage.setItem("carts",JSON.stringify(carts));

      //vuexlocalStorage
      state.carts = carts;



    },
    minusCount(state, id){
      state.carts.forEach(v=>{
        if(v.id == id ){
          v.count --;
        }
      })

      localStorage.setItem("carts",JSON.stringify(state.carts));
    },
    addCount(state, id){
      state.carts.forEach(v=>{
        if(v.id == id ){
          v.count ++;
        }
      })
      localStorage.setItem("carts",JSON.stringify(state.carts));
    },
    del(state, id){
      var index = state.carts.findIndex(v => {
        return v.id == id;
      })

      state.carts.splice(index, 1);
      localStorage.setItem("carts",JSON.stringify(state.carts));
    },
    dlorder(){
      var index = state.indent.findIndex(v => {
        return v.id == id;
      })

      state.indent.splice(index, 1);
      localStorage.setItem("indent",JSON.stringify(state.indent));
    }

  },
  // mutations 
  getters: {
    //Getters  Store .
    countAnother: function (state) {
      return state.navShow
    },
    getToken: function (state) {
      return state.token
    },
    getUserInfo: function (state) {
      return state.user
    },
    cartsTotalCount: state => {
      var carts = localStorage.getItem("carts");
      state.carts = carts ? JSON.parse(carts) : [];

      var result = 0;
      state.carts.forEach(v => {
        result += v.count;
      })
      return result;


    },
    //
    counts: state => {
      var obj = {};
      state.carts.forEach(v => {
        obj[v.id] = v.count;
      })
      return obj;
    }
  }




})

